/* || ##ANIMATION */
@use 'variables';

.animation__container {
    display: block;
    margin-right: auto;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: 10px;
}

.animation {
    margin-left: auto;
    margin-right: auto;
    width: 80vw;
    border-width: 3px;
    position: relative;
    animation-name: change-color;
    animation-duration: 3s;
    animation-direction: alternate-reverse;
    animation-iteration-count: infinite;
}

@keyframes change-color {
    0% {
        background-color: variables.$primary-color;
        border-style: solid;
    }

    25% {
        background-color: variables.$tertiary-color;
        border-style: dashed;
    }

    50% {
        background-color: variables.$primary-color;
        border-style: double;
        top: 0px;
    }

    75% {
        background-color: variables.$tertiary-color;
        border-style: solid;
    }

    100% {
        background-color: variables.$primary-color;
        border-style: dashed;
    }
}
