/* || ##SITE NAVIGATION BAR */
@use 'variables';

.primary-nav {
    position: fixed;
    top: 5.0vh;
    scroll-margin-top: 16vh;
    width: 100%;
    @include variables.flexbox ($justify-content: space-around);

}

.primary-nav__nav {
    font-weight: bold;
    font-size: clamp(1em, 3.5vw, 1.8em);
    width: 100%;
}

.primary-nav__ul {
    padding: 0.2em 2.5%;
    list-style-type: none;
    @include variables.flexbox ($justify-content: space-around);
    gap: 1em;
    width: max-content;
    //background-color: rgba(35, 187, 207, 0.2);
    background-color: rgba(variables.$secondary-color, 0.2);
    margin-right: 1em;
    width: 100%;
}

.primary-nav__a:hover {
    color: variables.$secondary-color;
    text-decoration: underline;
}