/* || ##HEADER */
@use 'variables';

.header__section {
    position: fixed;
    height: 5vh;
    @include variables.flexbox ($justify-content: space-around);
    font-size: clamp(0.9em, 1.5vw, 1.2em);
    width: 100%;
}


.header__date {
    height: fit-content;
    border-radius: 0.3em;
    /* display: flex;
    justify-content: space-around;
    align-items: center; */
    @include variables.flexbox ($justify-content: space-around);
    margin-right: 1em;
}