/* #BUTTONS */
.button {
  border-style: double;
  border-width: 0.23em;
  padding: 0 0.5em;
  display: inline-block;
  color: unset;
  background-color: unset;
  border-color: unset;
}

.button:hover {
  border-style: dotted;
  color: unset;
}

.button-primary {
  border-radius: 3em;
}

.button-secondary {
  border-radius: 0.3em;
}

.button-primary:last-child {
  margin-right: 1em;
}

