/* #CARDS */
.card--lg {
  height: 330px;
  width: 300px;
  border-radius: 5%;
  border-width: 0.4em;
  border-style: outset;
  padding: 0 5px;
  border-color: rgb(35, 187, 207);
  /*Removing this colour property will enable the dark mode toggle to work*/
  /*box-shadow: 5px 5px 10px, -5px -5px 10px, -5px 5px 10px, 5px -5px 10px;*/
}

.card--md {
  height: 130px;
  width: 130px;
  border-radius: 50%;
  border-width: 0px;
  border-style: solid;
  /* border-color: rgb(23, 125, 143); Removing this colour property will enable the dark mode toggle to work*/
  box-shadow: 3px 3px 10px, -3px -3px 10px, -3px 3px 10px, 3px -3px 10px;
}

.card--sm {
  margin: auto;
  height: 100px;
  width: 100px;
  border-radius: 10%;
  border-width: 0px;
  border-style: solid;
  /* border-color: rgb(23, 125, 143); Removing this colour property will enable the dark mode toggle to work */
  box-shadow: 3px 3px 10px brown, -3px -3px 3px brown, -3px 3px 10px brown, 3px -3px 10px brown;
}

.card--xsm {
  height: 40px;
  width: 110px;
  border-radius: 10%;
  border-width: 7px;
  border-style: outset;
  border-color: rgb(35, 187, 207);
  box-shadow: 5px 5px 10px, -5px -5px 10px, -5px 5px 10px, 5px -5px 10px;
}

/* #IMAGE CONFIGURATIONS/DEFINITIONS */
.img--lg {
  display: block;
  margin: auto;
  margin-top: 5px;
  height: 330px;
  width: 310px;
  border-radius: 50%;
}

.img--md {
  display: block;
  height: 90px;
  width: 180px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 0px;
  border-radius: 5%;
  /* box-shadow: 1px 1px 5px, -1px -1px 5px, -1px 1px 5px, 1px -1px 5px; */
}

.img--sm {
  height: 60px;
  width: 60px;
  margin-top: 34px;
  border-radius: 20%;
}

.img--xsm {
  height: 50px;
  width: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  margin-bottom: auto;
  border-radius: 20%;
}

/* #IMAGE CONTAINERS */
.img-wrapper--exterior {
  display: block;
  margin: auto;
  min-width: 300px;
}

.img-wrapper--interior {
  display: block;
  margin: auto;
  height: 350px;
  width: 330px;
  border-radius: 50%;
  border-width: 5px;
  border-style: double;
  border-color: rgb(35, 187, 207);
  box-shadow: 3px 3px 10px, -3px -3px 10px, -3px 3px 10px, 3px -3px 10px;
}

/* #ICONS */
.icons {
  font-size: larger;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

