/* || ##HEADER */
.header__section {
  position: fixed;
  height: 5vh;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  font-size: clamp(0.9em, 1.5vw, 1.2em);
  width: 100%;
}

.header__date {
  height: fit-content;
  border-radius: 0.3em;
  /* display: flex;
  justify-content: space-around;
  align-items: center; */
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  margin-right: 1em;
}

/* || ##SITE NAVIGATION BAR */
.primary-nav {
  position: fixed;
  top: 5vh;
  scroll-margin-top: 16vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.primary-nav__nav {
  font-weight: bold;
  font-size: clamp(1em, 3.5vw, 1.8em);
  width: 100%;
}

.primary-nav__ul {
  padding: 0.2em 2.5%;
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  gap: 1em;
  width: max-content;
  background-color: rgba(0, 128, 128, 0.2);
  margin-right: 1em;
  width: 100%;
}

.primary-nav__a:hover {
  color: teal;
  text-decoration: underline;
}

