}

/* || ##ANIMATION */
.animation__container {
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-top: auto;
  margin-bottom: 10px;
}

.animation {
  margin-left: auto;
  margin-right: auto;
  width: 80vw;
  border-width: 3px;
  position: relative;
  animation-name: change-color;
  animation-duration: 3s;
  animation-direction: alternate-reverse;
  animation-iteration-count: infinite;
}

@keyframes change-color {
  0% {
    background-color: rgb(35, 187, 207);
    border-style: solid;
  }
  25% {
    background-color: brown;
    border-style: dashed;
  }
  50% {
    background-color: rgb(35, 187, 207);
    border-style: double;
    top: 0px;
  }
  75% {
    background-color: brown;
    border-style: solid;
  }
  100% {
    background-color: rgb(35, 187, 207);
    border-style: dashed;
  }
}
